<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/wechatAutoResponse/keywordReplyList" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>

    <div class="cms-form-layout">
      <el-form ref="form" :rules="rules" :model="formData" label-width="110px">
        <el-form-item label="规则名称:" prop="ruleName">
          <el-input v-model="formData.ruleName" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="匹配优先级:" prop="sortNum">
          <el-input v-model.number="formData.sortNum" maxlength="2"></el-input>
          <div class="gray">在多组规则关键词都匹配的情况下，优先匹配优先级越大的</div>
        </el-form-item>
        <el-form-item label="是否禁用" prop="isEnable">
          <cms-switch v-model="formData.isEnable"></cms-switch>
          <div class="gray">设置为“是”，标识当前规则暂不生效</div>
        </el-form-item>
        <h3 class="cms-segmentation">关键词列表</h3>
        <el-table ref='dataTable' :data="keywordList" class="keyword-table">
          <el-table-column label="关键词">
            <div slot-scope="scope">
              <el-input v-model="scope.row.value" style="width:60%" placeholder="请输入匹配的关键词" maxlength="50"></el-input>
            </div>
          </el-table-column>
          <el-table-column prop="matchingRule" label="匹配规则">
            <div slot-scope="scope">
              <cms-switch v-model="scope.row.type" on-text='完全' off-text='模糊' size='large'></cms-switch>
            </div>
          </el-table-column>
          <el-table-column label="操作" width='175' fixed="right" align="center">
            <div slot-scope="scope">
              <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.$index)">删除</cms-button>
            </div>
          </el-table-column>
        </el-table>
        <div @click="addKeyword()" class="addKeyword">＋ 添加关键词</div>
        <div style="width:100%;cursor:pointer;">
          <h3 class="cms-segmentation">触发后回复内容
          </h3>
          <media-warpper :show="true" @change="getMediaData" style="margin:0" v-if="status"
          :data='formData.wechatMaterial'
          ></media-warpper>
        </div>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update(true)" v-color>修改</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import rules from '@/rules'
import mediaWarpper from './../materialLibrary/components/mediaWarpper'
import { keywordUpdate, keywordGet } from '@/api/wechat/autoResponse.js'
var defaultData = {
  'id': '',
  'isEnable': true,
  'mediaId': '',
  'msgType': '',
  'ruleName': '',
  'sortNum': null,
  'thumbMediaId': '',
  'hqMusicUrll': '',
  'musicUrl': '',
  'description': '',
  'title': '',
  'content': '',
  'wechatReplyKeywordList': []
}
export default {
  name: 'keywordSave',
  mixins: [form],
  components: {
    mediaWarpper
  },
  data() {
    return {
      formData: Object.assign({}, defaultData),
      keywordList: [],
      rules: {
        // 新增规则
        ruleName: [rules.required('请填写规则名称')],
        sortNum: [rules.required('请填写匹配优先级'), rules.number()]
      }
    }
  },
  methods: {
    update(state) {
      const params = this.$cms.deepCopy(this.formData)
      const wechatReplyKeywordList = []
      this.keywordList.forEach(v => {
        if (v.type) {
          v.wordkeyLike = ''
          v.wordkeyEq = v.value
        } else {
          v.wordkeyLike = v.value
          v.wordkeyEq = ''
        }
        wechatReplyKeywordList.push(v)
      })
      params.wechatReplyKeywordList = wechatReplyKeywordList
      delete params.wechatMaterial
      this.updateForm(
        keywordUpdate,
        params,
        '/wechatAutoResponse/keywordReplyList',
        'list'
      )
    },
    getFormData(id) {
      // 通过id获取表单信息
      keywordGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
        if (res.data.msgType === 'text') {
          this.formData.wechatMaterial = {
            mediaType: 'text',
            content: res.data.content
          }
        }
        if (res.data.msgType === 'music') {
          this.formData.wechatMaterial = {
            mediaType: 'music',
            title: res.data.title,
            musicUrl: res.data.musicUrl,
            hqMusicUrll: res.data.hqMusicUrll
          }
        }
        const keywordList = res.data.wechatReplyKeywordList
        this.formData.wechatReplyKeywordList = res.data.wechatReplyKeywordList
        if (keywordList.length > 0) {
          keywordList.forEach(v => {
            const object = Object.assign({}, v)
            if (v.wordkeyEq === '') {
              object.value = v.wordkeyLike
              object.type = false
            } else {
              object.value = v.wordkeyEq
              object.type = true
            }
            this.keywordList.push(object)
          })
        }
      })
    },
    deleteData(index) {
      this.keywordList.splice(index, 1)
    },
    addKeyword() {
      this.keywordList.push({
        value: '',
        type: false
      })
    },
    getMediaData(items) {
      if (items[0]) {
        this.formData.mediaId = items[0].id
        this.formData.msgType = items[0].mediaType
        this.formData.appId = items[0].appId
      } else {
        this.formData.hqMusicUrll = items.hqMusicUrll
        this.formData.musicUrl = items.musicUrl
        this.formData.thumbMediaId = items.thumbMediaId
        this.formData.description = items.description
        this.formData.title = items.title
        this.formData.content = items.content
        this.formData.msgType = items.msgType
      }
    },
    rest() {
      this.$refs.form.resetFields()
      this.destroyed()
      this.formData = this.$cms.deepCopy(defaultData)
      this.keywordList = []
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
    this.formData.id = this.$route.query.id
  }
}
</script>
<style scoped>
.keyword-table {
  width: 100%;

  border-left: 1px solid #f7f7f7;
  border-right: 1px solid #f7f7f7;
}
.type_ul {
  padding: 20px;
  list-style: none;
}
.type_ul li {
  width: 100px;
  height: 50px;
  text-align: center;
  float: left;
}
.type_ul li img {
  width: 30px;
  height: 30px;
}
.type_p {
  font-size: 18px;
  color: #000000;
}
.type_p span {
  font-size: 14px;
  color: #646464 !important;
}
.addKeyword {
  width: 100%;
  font-size: 14px;
  cursor: pointer;
  border: 1px solid #f7f7f7;
  height: 30px;
  text-align: center;
  line-height: 30px;
}
</style>



